@import "../../../../css/module/fn";
@import "../../../../css/module/color";

#swiper {
  //display: inline-block;
  width: 900px;
  height: 600px;
  margin: auto;
  background-color: #fff;
  @include shadow();

  .swiper-slide {
    padding: 20px 50px;
    .title {
      padding: 8px 20px;
      background-color: $red;
      color: #fff;
      font-size: 28px;
      font-weight: 100;
      @include textClip;
    }
    .pMsg {
      font-size: 14px;
      margin-top: 1em;
      word-wrap:break-word;
    }
  }

  #buttons {
    position: absolute;
    z-index: 100;
    bottom: 0;
    border-top: 1px solid #aaa;
    width: 100%;
    text-align: center;
    padding: 10px;

    button {
      font-size: 14px;
      padding: 10px 60px;
      text-align: center;
      border-radius: 6px;

      &#like {
        background-color: $red;
        color: #fff;
        margin-right: 20px;
      }
    }
  }
}

.swiper-pagination {
  text-align: right;
  padding-right: 2em;
}



.resultContainer {
  background-color: #fff;
  padding: p2m(70);
  text-align: center;

  h1 {
    text-align: center;
    font-size: 20px;
    line-height: 1.5em;
    width: 250px;
    margin: 0 auto;
    border-bottom: 5px double $red;
  }

  .imgRs {
    display: block;
    width: 92px;
    height: 137/2 ;
    margin: 30px auto 20px;
  }

  .progressBar {
    width: p2m(250);
    height: p2m(8);
    margin: 0 auto;
    background-color: #ddd;
    border-radius: 20px;

    .progress {
      background-color: $red;
      height: p2m(8);
      border-radius: 20px;
    }
  }

  .res {
    font-size: 48px;
    line-height: 1em;
    color: $red;
    margin-top: 0;
    margin-bottom: 40px;
  }

  table {
    width: 100%;
    td {
      text-align: left;
      width: 36%;


      &.t {
        color: #999;
      }
      &.r {
        color: #222;
        font-size: 18px;
        line-height: 28px;
      }

      img {
        height: 32px;
      }
    }

  }
}

.adddiv{
  border-radius: 5px;
  background-color: #999;
  color:#ffffff;
  width:200px;
  height:50px;
  text-align: center;
}
